@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
html {
  background-color: rgb(255, 252, 247);

}

* {
    font-weight: 300;
    font-family: 'Questrial', sans-serif;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

button {
  border: none;
  padding: 1em;
  border-radius: 5px;
  background-color: rgb(89, 94, 112);
  color: white;
  transition: 0.5s ease;
  font-size: 20px;
  font-weight: 400;
}

button:hover {
  transform: scale(1.01);
  cursor: pointer;
  background-color: rgb(15, 15, 15);
  color: rgb(235, 235, 235);
}

.banner{
    background-image: url(/mad-libs-frontend/static/media/Banner.e5b39338.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: 0 auto;
    height: 15vh;
    width: 30%;
    overflow: visible;
}

.MongoMisfits {
    background-image: url(/mad-libs-frontend/static/media/MongoMisfits.d52ddcf8.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 50vh;
}

  

.gameInputMain input {
    border: none;
    font-size: 50px;
    text-align: center;
    color: white;
    width: 100%;
    outline: none;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameInputMain input:-ms-input-placeholder {
    color: white;
}

.gameInputMain input::placeholder {
    color: white;
}

.body-page{
    color: rgb(101, 161, 141);
}

.gameBodyMain {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
    grid-gap: 1em;
    gap: 1em;
    position: relative;
}

.iconDiv1, .iconDiv2 {
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hiddenIconDiv {
    display: none;
}

.arrowIcon {
    font-size: 80px;
    cursor: pointer;
    opacity: 1;
    transition: 0.3s ease-in-out;
}

.arrowIcon:hover {
    transform: scale(1.1);
}

.arrowIconInvisible {
    opacity: 0;
    cursor: none;
}

.gameInputMain {
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameInput {
    opacity: 0;
    transition: 0.5s ease-in-out;
    position: fixed;
    height: 40vh;
    width: 30%;
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(165, 165, 165);
    border-radius: 20px;
    pointer-events: none;
    transform: translateY(200px);
    z-index: -1;
    background: linear-gradient(45deg, rgb(119, 161, 200) 0%, rgb(36, 36, 36) 100%);
}

.activePage {
    opacity: 1;
    pointer-events: visible;
    transform: translateY(0);
    z-index: 1;
    transition: 0.3s ease;
    background-position: center;
}

.activePage:hover {
    transform: scale(1.02);
    filter: drop-shadow(1px 1px 15px rgb(146, 146, 146));
    background-position: 200% center;
}

.gameInputMain {
    width: 33%;
    right: 0;
    overflow: hidden;
}

.gameIndexIndicator {
    width: 100%;
    font-size: 30px;
}

.gameIndexIndicator h1 {
    padding: 0;
    margin: 0 0 10px 0;
}

.underlinedWords {
    text-decoration: underline;
}

.gameSubmit {
    font-size: 20px;
    width: 50%;
}

@media only screen and (max-width: 768px) {
    .gameBodyMain {
        padding: 5em 0 0 0;
        flex-wrap: wrap;
        height: 50vh;
    }

    .gameInput {
       width: 85%;
    }

    .gameInputMain {
        width: 80%;
    }

    .iconDiv1 {
        display: none;
    }

    .hiddenIconDiv {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .hiddenIconDiv, .iconDiv2 {
        width: 40%;
    }
    
    .gameInputMain input {
        font-size: 20px;
    }

    .gameSubmit {
        width: 90%;
        font-size: 16px;
    }

    .gameIndexIndicator h1 {
        font-size: 30px;
    }
}

.hamburger {
    display: none;
}

.headerLinks{
    display: flex;
    justify-content: center;
    font-family: 'Unica One', cursive;
}

.headerLinks h2{
    padding: 0 15px;
}

.headerLinks h2:hover {
    color: white;
}

a {
    text-decoration: none;
    margin: 1em;
    text-align: center;
    border-radius: 5px;
    transition: 0.2s ease-in-out;
    color: black
}
  
a:hover {
    transform: scale(1.02);
    background-color: rgb(53, 53, 53);
    color: white;
}

@media only screen and (max-width: 768px) {
    .headerLinks h2{
        font-size: 20px;
        border-bottom: none;

    }

    .hamburger {
        display: inline-block;
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 30px;
    }

    .headerLinks h2 {
        font-weight: 700;
        color: rgb(90, 90, 90);
    }

    .headerLinks {
        flex-direction: column;
        align-items: center;
        height: 0vh;
        opacity: 0;
        transition: 0.3s ease;
        pointer-events: none;
    }
    .active {
        height: 100%;
        opacity: 1;
        pointer-events: visible;
    }

    .overlay {
        opacity: 1;
        background-color: gray;
        position: absolute;
        width: 100vw;
        height: 100vh;
        z-index: 5;
    }
}
.resultsMain {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    grid-gap: 2em;
    gap: 2em;
}

.resultsBody {
    text-align: left;
    margin: 2em 0 0 0;
    padding: 1em;
    width: 40%;
    font-size: 26px;
    border: 1px solid gray;
    border-radius: 15px;
    background-color: white;
    transition: 0.3s ease;
}

.resultsBody:hover, .otherUsers:hover {
    transform: scale(1.03);
    filter: drop-shadow(0px 2px 3px rgb(204, 204, 204));

}

.resultsBtnArr {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
}

.resultsBtnArr a {
    padding: 0;
    margin: 0;
}

.resultsMain form {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
    width: 50%;
}

.resultsMain form input {
    width: 40%;
    border: 1px solid gray;
    padding: 1em;
    font-size: 20px;
    border-radius: 15px;
}

.resultsDivider {
    background-color: rgb(73, 85, 98);
    width: 100vw;
    height: 10vh;
    color: white;
}

.otherUsersArrMain {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 85%;
    grid-gap: 1em;
    gap: 1em;
    margin: 0 0 10em 0;
}

.otherUsers {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 40%;
    aspect-ratio: 1;
    padding: 1em;
    border: 1px solid rgb(233, 233, 233);
    background-color: white;
    border-radius: 15px;
    transition: 0.3s ease;
    color: black;
    text-align: left;
}

.otherUsers h1, .otherUsers h2 {
    font-size: 40px;
}

.otherUsers p {
    font-size: 30px;
}

.errorMessage {
    color: red;
    animation: errorMsg 1s infinite ease-in-out;
}

@keyframes errorMsg {
    100%, 0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

@media only screen and (max-width: 768px) {
    .resultsBody {
        width: 75%;
        font-size: 16px;
    }

    .resultsMain button {
        font-size: 16px;
    }

    .resultsMain form {
        flex-direction: column;
        width: 75%;
    }

    .resultsMain form button {
        width: 100%;
    }

    .resultsMain form input {
        font-size: 16px;
        width: 100%
    }

    .otherUsers {
        width: 75%;
        align-items: flex-start;
    }

    .otherUsers h1, h2 {
        font-size: 30px;
        margin: 0;
        border-bottom: 1px solid rgb(185, 185, 185);;
    }

    .otherUsers p {
        font-size: 16px;
    }
}
.instructionsMain {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    text-align: left;
}

@media only screen and (max-width: 768px) {
    .instructionsMain {
        font-size: 20px;
    }
}
.users-templates ul{
    border: 1px solid black;
    list-style: none;
}

.users-templates li{
    font-weight: 700;
    font-size: 18px;
}
.users-templates span{
    color: red;
    font-weight: 600;
    font-size: 20px;
}

#tempform {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    flex-wrap: wrap;
    width: 50vw;
    margin: 0 auto;
}

#tempform > * {
    border: 1px solid rgb(206, 206, 206);
    border-radius: 15px;
    padding: 1em;
    font-size: 20px;
    text-align: center;
}

#tempform input {
    width: 100%;
}

#tempform textarea {
    text-align: left;
    width: 40%;
    aspect-ratio: 1;
}

#tempform button {
    width: 100%;
}

@media only screen and (max-width: 768px) {
    #tempform {
        width: 75%;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    #tempform textarea, #tempform input, #tempform button {
        aspect-ratio: 0;
        width: 100%;
        font-size: 16px;
    }
}
